body {
  margin: 0;
  padding: 0;
  font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;

  background-color:black;
  color: white;
}

.home {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.intro {
  position: absolute;
  top: 30%;
  z-index: 1;
  -webkit-animation: intro 6s ease-out 1s;
          animation: intro 6s ease-out 1s;
  color: rgb(75, 213, 238);
  font-weight: 400;
  font-size: 200%;
  opacity: 0;
}

@-webkit-keyframes intro {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes intro {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

p {
  color: rgb(229, 177, 58) !important;
}

/* Set the font, lean the board, position it */
.board {
  font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
  -webkit-transform: perspective(300px) rotateX(25deg);
          transform: perspective(300px) rotateX(25deg);
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  text-align: justify;
  position: absolute;
  margin-left: -9em;
  font-weight: bold;
  overflow: hidden;
  font-size: 350%;
  height: 50em;
  width: 18em;
  bottom: 0;
  left: 50%;
}

.board:after {
  position: absolute;
  content: " ";
  bottom: 60%;
  left: 0;
  right: 0;
  top: 0;
}

/* Set the scrolling animation and position it */
.content {
  -webkit-animation: scroll 100s linear 6s;
          animation: scroll 100s linear 6s;
  position: absolute;
  top: 100%;
}

#title,
.subtitle {
  text-align: center;
}

@-webkit-keyframes scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -170%;
  }
}

@keyframes scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -170%;
  }
}

@media screen and (max-width: 576px) {
  .intro {
    font-size: 100%;
    font-weight: 150;
  }
  .board {
    font-size: 150%;
    font-weight: 250;
    color: green;
    padding: 0 20px;
  }
}

